import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route, Router } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import PubSub, { topics } from '../../lib/pubsub';
import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

import DogSimplePublic from '../../components/dog/simplePublic';
import ImageScroller from '../../components/imageScroller';
// import TeamConnectConfirm from '../../components/team/connect-confirm';
import TeamLine from '../../components/team/publicLine';
import PublicTeam from '../../components/team/public';

import Overlay from '../../components/overlay/';
import DogPublicDetail from '../dogs/publicDetail';

import MiniViewGraph from '../../components/dog/miniViewGraph';

const initialState = {
    newTeam: {},
};

@observer
class TeamsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
        this.time = new Date().getTime();
        this.apiServer = util.getApiServer();
    }

    async loadPublicView(props) {
		this.setState({ isLoading: true });
        const { teamid } = props;
		const { teamStore } = this.props.stores;
        await teamStore.getPublicViews(teamid);
		this.setState({ isLoading: false });
	}

    async loadAll(props = this.props) {
        await this.loadPublicTeam(props);
        await this.loadPublicView(props);
    }

    async loadPublicTeam(props = this.props) {
		const { teamStore } = props.stores;
        const { teamid } = props;
        await teamStore.loadPublicDetail({ id: teamid });
    }

    // Overlay functions
    onDogClick = (e, dogid) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            showDog: dogid,
        });
        return false;
    }

    hideDog = () => {
        this.setState({
            showDog: null,
        });
    }

    onUserClick = (e, userid) => {
        e.preventDefault();
        e.stopPropagation();
        return false;
    }
    // /Overlay functions

    subscribeNotificationTopic = async (field) => {
		const { userStore } = this.props.stores;
        const { user } = userStore;
        const { notificationTopics = {} } = user;
        const value = 1;
        await userStore.setNotificationTopic({
            field: `notificationTopics.${field}`,
            value,
        });
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }
    
	componentDidMount() {
        const { back = this.back } = this.props;
        const { appState } = this.props.stores;
        appState.setSwipeRight(() => {
            appState.setSwipeRight();
            back();
        });
        this.loadAll();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.teamid !== this.props.teamid) {
            this.loadAll(nextProps);
        }
    }

	render() {
        const { showDog } = this.state;
		const { teamStore, userStore } = this.props.stores;
        const { user, isAdmin } = userStore;
        const { language = 'en' } = user;
		const { publicTeam, publicTeamFollowers, publicViewsGraphData, publicViewsGraphLegend } = teamStore;
        const isTeamAdmin = (publicTeam && publicTeam.members) ? publicTeam.members.some(m => m.id === user.id) : false;

        const { marginTop, marginBottom, back = this.back, hideTeam = () => {} } = this.props;

        const defaultImage = {
            s3XXLargeLink: './assets/homerun-race-logo.png',
        };

        const {
            dogs = [],
            users = [],
            image,
            imageHeader,
            publicWorkouts,
            publicWorkoutDetails,
            publicWorkoutSummary,
            publicTrophys,
            publicMemories,
            publicDogs,
        } = publicTeam;

        const headerImage = imageHeader && imageHeader.s3XXLargeLink ? imageHeader : defaultImage;
        const teamImage = image && image.s3LargeLink ? image : null;
        const teamAmbition = userStore.findTeamAmbition({ ambition: publicTeam.ambitions, language });
        const teamType = userStore.findTeamType({ type: publicTeam.type, language });
        const isYourTeam = publicTeam.members && publicTeam.members.findIndex(e => e.id === user.id) > -1;
        const isFollowing = publicTeam && publicTeam.followers && publicTeam.followers.indexOf(user.id) > -1;
        const hasRequestedFollow = publicTeam.followRequests && publicTeam.followRequests.findIndex(e => e.id === user.id) > -1;

        const { notificationTopics = {} } = user;
        const topic = `team-${publicTeam.id}`;

		return (<>
            {showDog && <Overlay>
                <DogPublicDetail stores={this.props.stores} dogid={showDog} marginTop={'0px;'} marginBottom={'0px;'} back={this.hideDog} dogs={dogs} />
            </Overlay>}

			<div class='container-fluid pb-5' style={`margin-bottom: ${marginBottom || MARGIN_BOTTOM}; margin-top: ${marginTop || MARGIN_TOP};`}>
                <button class='btn btn-link pl-0 fixed-top text-primary'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                    onClick={back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>

                <div class='row mb-0'>
                    <div class='w-100 position-relative'
                        style={`height: 25vh; background-size: cover; background-repeat: no-repeat; background-position: 50% 50%; background-color: #a0a0a0; ${headerImage ? `background-image: url("${headerImage.s3XXLargeLink}");` : ''}`}
                    >
                        {isYourTeam && <>
                            <a class='btn btn-sm btn-primary position-absolute'
                                style={`top: 5px; right: 10px;`}
                                href={`/teams/edit/${publicTeam.id}`}
                            >
                                <i class='fas fa-edit' /> <Text id='teams.edit'>Endre</Text>
                            </a>
                        </>}                    
                    </div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                        <div class='row'>
                            <div class='col-3 col-sm-4 col-md-3 col-lg-4 position-relative'>
                                <div
                                    class={`text-center border border-white rounded-circle position-absolute imageRounded imageRoundedResponsive imageRoundedResponsivePosition`}
                                    style={`left: 20px; ${teamImage ? `background-image: url("${teamImage.s3LargeLink}"); background-size: cover; background-repeat: no-repeat; background-position: 50% 50%;` : ''}`}
                                >
                                    {!teamImage && <i class='fas fa-users text-muted mt-3' style='font-size: 40px;' />}
                                </div>
                                &nbsp;
                            </div>
                            <div class='col-9 col-sm-8 col-md-9 col-lg-8' style='line-height: 1.2em;'>
                                <h5 class='my-0'>{publicTeam.name}</h5>
                                <small class='text-muted'>
                                    {publicTeam.place ? `${publicTeam.place}, ` : ''}{publicTeam.country}
                                </small><br />
                                {teamType && <span class='badge badge-pill badge-secondary mt-2'>{teamType}</span>}
                                {teamAmbition && <span class='badge badge-pill badge-secondary mt-2 ml-2'>{teamAmbition}</span>}
                                <h6 class='mt-2'>
                                    <span class='mr-3'><span class='font-weight-bolder'>{publicTeam.followers ? publicTeam.followers.length : 0}</span> <Text id='teams.followers'>followers</Text></span>
                                    <span class='mr-3'><span class='font-weight-bolder'>{publicTeam.dogs ? publicTeam.dogs.length : 0}</span> <Text id='teams.dogs-menu'>dogs</Text></span>
                                </h6>
                            </div>
                        </div>
                    </div>
                    {(!notificationTopics || !notificationTopics[topic]) && <>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                            <button class='btn btn-block btn-primary btn-sm' onClick={() => this.subscribeNotificationTopic(topic)}>
                                <Text id='team.notifications'>Subscribe to team notifications</Text>
                            </button>                    
                        </div>
                    </>}
                </div>

				<div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        {!isYourTeam && !isFollowing && !hasRequestedFollow && publicTeam.public > 0 && <div class='clearfix mb-2'>
                            <PublicTeam stores={this.props.stores} team={publicTeam} buttonOnly={true} skipHome={true} />
                        </div>}
                    </div>
                </div>

                {/* <TeamConnectConfirm stores={this.props.stores} /> */}

				<div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.socialmedia'>Sosialemedier</Text></h5>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <div class='d-flex justify-content-center'>

                            {isYourTeam && !publicTeam.facebook && <>
                                <div class='text-center text-muted'>
                                    <div class='display-1'>
                                        <i class='fab fa-facebook' />
                                    </div>
                                    <h3 class='font-weight-lighter'>
                                        <Text id='teams.add-sosialmedia'>Gå inn på endring av teamet for å legge inn en sosialemedier.</Text>
                                    </h3>
                                </div>
                            </>}

                            {publicTeam.url && <div class='mx-2'><h3><a href={mu.asUrl(publicTeam.url)} target='_blank' rel='noreferrer'><i class='fas fa-link' /></a></h3></div>}
                            {publicTeam.facebook && <div class='mx-2'><h3><a href={mu.asFacebookLink(publicTeam.facebook)} target='_blank' rel='noreferrer'><i class='fab fa-facebook' /></a></h3></div>}
                            {publicTeam.instagram && <div class='mx-2'><h3><a href={mu.asInstagramLink(publicTeam.instagram)} target='_blank' rel='noreferrer'><i class='fab fa-instagram' /></a></h3></div>}
                            {publicTeam.snapchat && <div class='mx-2'><h3><a href={mu.asSnapchatLink(publicTeam.snapchat)} target='_blank' rel='noreferrer'><i class='fab fa-snapchat' /></a></h3></div>}
                        </div>
                        <div class='d-flex justify-content-center mt-3'>
                            <div class='mx-2'><h3><a class='btn btn-outline-success' href={`/stories/team/${publicTeam.uuidv4}`}><i class='fas fa-rss' /> <Text id='story.all-stories'>Stories</Text></a></h3></div>
                            <div class='mx-2'><h3><a class='btn btn-outline-primary' href={`/teams/workouts/team/${publicTeam.uuidv4}`} onClick={hideTeam}><i class='fas fa-running' /> <Text id='story.all-workouts'>All workouts</Text></a></h3></div>
                        </div>
                    </div>
                </div>

                {publicTeam.imagesSponsor && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.public-sponsor-images' fields={{ total: publicTeam.imagesSponsor.length }}>Sponsorer - {publicTeam.imagesSponsor.length} stk</Text></h5>
                    </div>
                    <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative px-0`}>
                        <ImageScroller stores={this.props.stores} images={publicTeam.imagesSponsor} />
                    </div>
                </div>}

                {publicTeam.sponsors && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.sponsors'>Sponsors</Text></h5>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        {isYourTeam && !publicTeam.sponsors && <>
                            <div class='text-center text-muted'>
                                <div class='display-1'>
                                    <i class='fas fa-pen-nib' />
                                </div>
                                <h3 class='font-weight-lighter'>
                                    <Text id='teams.add-sponsors'>Gå inn på endring av teamet for å legge inn sponsorer.</Text>
                                </h3>
                            </div>
                        </>}
                        <Markdown markdown={mu.replaceImages(publicTeam.sponsors, publicTeam.imagesSponsor, language)} markdownOpts={mu.getMarkdownOptions()} />
                    </div>
                </div>}


				<div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.description'>Beskrivelse</Text></h5>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        {isYourTeam && !publicTeam.description && <>
                            <div class='text-center text-muted'>
                                <div class='display-1'>
                                    <i class='fas fa-pen-nib' />
                                </div>
                                <h3 class='font-weight-lighter'>
                                    <Text id='teams.add-description'>Gå inn på endring av teamet for å legge inn en beskrivelse.</Text>
                                </h3>
                            </div>
                        </>}
                        <Markdown markdown={mu.replaceImages(publicTeam.description, publicTeam.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                    </div>
                </div>

				{publicTeam.descriptionWorkoutplan && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.description-workoutplan'>Workoutplan</Text></h5>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        {isYourTeam && !publicTeam.descriptionWorkoutplan && <>
                            <div class='text-center text-muted'>
                                <div class='display-1'>
                                    <i class='fas fa-pen-nib' />
                                </div>
                                <h3 class='font-weight-lighter'>
                                    <Text id='teams.add-description-workoutplan'>Gå inn på endring av teamet for å legge inn en treningsplan.</Text>
                                </h3>
                            </div>
                        </>}
                        <Markdown markdown={mu.replaceImages(publicTeam.descriptionWorkoutplan, publicTeam.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                    </div>
                </div>}

				{publicDogs && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.dogs' fields={{ total: dogs.length }}>Hundene - {dogs.length} stk</Text></h5>
                    </div>

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <div class='row'>
                            {dogs && dogs.map(dog => (
                                <DogSimplePublic stores={this.props.stores} dog={dog} showAge={true} className={'col'}
                                    onDogClick={this.onDogClick} 
                                />
                            ))}
                        </div>
                    </div>
                </div>}

				{publicTeam.images && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.public-photos' fields={{ total: publicTeam.images.length }}>Teambilder - {publicTeam.images.length} stk</Text></h5>
                    </div>
                    <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative px-0`}>
                        <ImageScroller stores={this.props.stores} images={publicTeam.images} />
                    </div>
				</div>}

                {publicMemories && publicTeam.achievements && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.achievements'>Achievements</Text></h5>
                    </div>
                    <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative`}>
                        <div class='row'>
                            {publicTeam.achievements && publicTeam.achievements.map(file => {
                                return (
                                    <div class='col-12'>
                                        {file.date && <span class='font-weight-lighter mr-2'>{util.isoDate(file.date, false, false, true)}</span>}
                                        <span class='mr-2 font-weight-bold'>{file.name}</span>
                                        <span class='mr-2 font-weight-light'>{file.description}</span>
                                        {file.url && <span class='mr-2'><a href={mu.asUrl(file.url)} target='_blank' rel='noreferrer' native><i class='fas fa-link' /></a></span>}
                                    </div>
                                );
                            })}
                        </div>
                    </div>
				</div>}

				{publicTrophys && publicTeam.trophys && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.trophy'>Trophys</Text></h5>
                    </div>
                    <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative`}>
                        <div class='row'>
                            {publicTeam.trophys && publicTeam.trophys.reverse().map(file => {
                                const img = mu.getTrophyImage(file.name);
                                if (!img) {
                                    return '';
                                }
                                const textHeader = file.descriptionHeader && file.descriptionHeader[language] ? file.descriptionHeader[language] : '';
                                const text = file.description && file.description[language] ? file.description[language] : '';
                                return (
                                    <div class='col-4 mt-3'>
                                        <div class='mb-2'>
                                            <img src={img} class='img-fluid' />
                                        </div>
                                        <strong>{textHeader}</strong>
                                        {/* <small class='font-weight-lighter mr-2'>
                                            {util.isoDate(file.createdDate, false, false, true)}
                                        </small> */}
                                        <small>
                                            <details>
                                                <summary><Text id='teams.trophy-details'>Details</Text></summary>
                                                <Markdown markdown={mu.replaceImages(text, publicTeam.images, language)} markdownOpts={mu.getMarkdownOptions()} />
                                            </details>
                                        </small>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
				</div>}

                {/* {util.isArray(publicTeamFollowers) && <div class='row mt-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.followers'>Followers</Text></h5>
                    </div>
                    <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative`}>
                        {util.isArray(publicTeamFollowers) && publicTeamFollowers.map(t => 
                            <TeamLine stores={this.props.stores} team={t} myTeamId={publicTeam.id} showBlockButton={false} callback={this.loadTeam} />
                        )}
                    </div>
                </div>} */}

				<div class='row mt-5 mb-5'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <div class='text-center text-muted'>
                            <div class='display-1'>
                                <i class='fas fa-ellipsis-h' />
                            </div>
                            <div style='line-height: 0.9em;'>
                                <small>
                                    <Text id='teams.something-missing'>Er det noe du savner på denne siden? Send oss en melding på Facebook.</Text>
                                </small>
                            </div>
                        </div>
                    </div>
                </div>

                {(isAdmin || isTeamAdmin) && <>
                    <h5 class='font-weight-lighter text-muted border-bottom'><Text id='teams.visits'>Team page visits</Text></h5>
                    <MiniViewGraph stores={this.props.stores}
                        dataLeft={publicViewsGraphData}
                        legendLeft={['team']}
                        key={`minigraph-team-${publicTeam.uuidv4}`}
                    />
                </>}

                {publicTeam.uuidv4 && <img src={`${this.apiServer}/api/teams/${publicTeam.uuidv4}/view?user=${user.id}&time=${this.time}`} style='width: 1; height: 1; opacity: 0.1;' />}
			</div>
		</>);
	}
}

export default TeamsDetail;
